.transactionHistory {
  font-size: 14px;
  border-collapse: collapse;
  text-align: center;
  width: 100%;
}

th,
td:first-child {
  background: #afcde7;
  color: white;
  padding: 10px 20px;
}

th,
td {
  border-style: solid;
  border-width: 0 1px 1px 0;
  border-color: white;
}

td {
  background: #d8e6f3;
}

th:first-child,
td:first-child {
  text-align: left;
}
